/* ----------------------------------------
    Stylesheet Guide
 ------------------------------------------

    1. Base
    2. Layout
    3. Components
    4. Pages

---------------------------------------------------------- */

/* ----------------------------------------
    1. Base
 ---------------------------------------- */

/* ----------------------------------------
        Reset
     ---------------------------------------- */

* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

article,
aside,
audio,
canvas,
command,
datalist,
details,
embed,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
meter,
nav,
output,
progress,
section,
source,
video,
main {
  display: block;
}

mark,
rp,
rt,
ruby,
summary,
time {
  display: inline;
}

a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
blockquote,
big,
body,
center,
canvas,
caption,
cite,
code,
command,
datalist,
dd,
del,
details,
dfn,
dl,
div,
dt,
em,
embed,
fieldset,
figcaption,
figure,
font,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
keygen,
label,
legend,
li,
meter,
nav,
object,
ol,
output,
p,
pre,
progress,
q,
s,
samp,
section,
small,
span,
source,
strike,
strong,
sub,
sup,
table,
tbody,
tfoot,
thead,
th,
tr,
tdvideo,
tt,
u,
ul,
var {
  background: transparent;
  border: 0 none;
  font-weight: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: top;
}

a {
  text-decoration: none;
  outline: none !important;
  vertical-align: baseline;
}

.material-icons,
.material-icons-outlined {
  font-size: inherit;
}

b,
strong {
  font-weight: bold;
}

ul,
ol {
  list-style: none;
}

q {
  quotes: none;
}

table,
table td {
  padding: 0;
  border: none;
  border-collapse: collapse;
}

img {
  vertical-align: top;
  max-width: 100%;
}

embed {
  vertical-align: top;
}

input,
textarea {
  font-family: inherit;
  -webkit-box-sizing: border-box !important;
  box-sizing: border-box !important;
}

input,
button {
  -webkit-appearance: none;
  outline: none;
  background: transparent;
}

button::-moz-focus-inner {
  border: 0;
}

th {
  text-align: left;
}

textarea {
  outline: none !important;
}

button {
  cursor: pointer;
  color: inherit;
  font-family: inherit;
}

html,
body {
  min-height: 100%;
}

fieldset,
legend,
label,
select,
input,
textarea,
form,
button,
optgroup {
  -webkit-appearance: none !important;
}

.container,
.container-fluid {
  padding-right: 1rem;
  padding-left: 1rem;
}

.row {
  margin-right: -1rem;
  margin-left: -1rem;
}

.row > [class*="col-"] {
  padding-left: 1rem;
  padding-right: 1rem;
}

.container.extra-width {
  max-width: 100%;
  padding-left: 2.75rem;
  padding-right: 2.75rem;
}

/* ----------------------------------------
        Global
    ---------------------------------------- */

body {
  background-color: #fff;
  overflow-x: hidden;
  overflow-y: initial !important;
}

.mad-page-wrapper {
  overflow: hidden;
}

html {
  overflow-y: initial !important;
  scroll-behavior: smooth;
}

::-webkit-selection {
  color: #fff;
  background-color: #2e3133;
}

::-moz-selection {
  color: #fff;
  background-color: #2e3133;
}

::selection {
  color: #fff;
  background-color: #2e3133;
}

mark {
  color: #fff;
  background-color: #2e3133;
}

.clearfix::after {
  content: "";
  display: block;
  clear: both;
}

.page-wrapper {
  overflow: hidden;
}

[data-bg-image-src] {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.animated {
  -webkit-animation-duration: 500ms;
  animation-duration: 500ms;
}

.w-100 {
  width: 100%;
}

.material-icons,
.material-icons-outlined {
  line-height: inherit;
  font-size: inherit;
}

.svg .cls-1 {
  stroke-width: 10px;
}

/* ----------------------------------------
        Typography
    ---------------------------------------- */

:root {
  font-size: 16px;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  line-height: 2rem;
  font-weight: 400;
  color: #515759;
}

address:not(:last-child) {
  margin-bottom: 1.625rem;
}

q,
kbd,
strong,
em,
tt,
var,
span,
del,
big,
small {
  vertical-align: baseline;
}

/* ----------------------------------------
            Headings
        ---------------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #2e3133;
  font-family: "Inter", sans-serif;
  font-weight: bold;
}

h1:not(:last-child),
h2:not(:last-child),
h3:not(:last-child),
h4:not(:last-child),
h5:not(:last-child) {
  margin-bottom: 1.4rem;
}

h1 {
  letter-spacing: -0.15rem;
}

h3:not(:last-child) {
  margin-bottom: 1.5rem;
}

h1 {
  font-size: 3.75rem;
  line-height: 4rem;
  font-weight: normal;
}

h2 {
  font-size: 3rem;
  line-height: 3.5rem;
  font-weight: normal;
  letter-spacing: -0.1rem;
}

h3 {
  font-size: 2.25rem;
  line-height: 3rem;
  font-weight: 500;
  letter-spacing: -0.025rem;
}

h4 {
  font-size: 1.875rem;
  line-height: 2.5rem;
  font-weight: 500;
  letter-spacing: -0.05rem;
}

h5 {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 600;
  letter-spacing: -0.025rem;
}

h6 {
  font-size: 1.125rem;
  line-height: 1.5rem;
}

h3:not(:last-child) {
  margin-bottom: 1.7rem;
}

h4:not(:last-child) {
  margin-bottom: 1.5rem;
}

h5:not(:last-child),
h6:not(:last-child) {
  margin-bottom: 1.45rem;
}

h5.mad-page-title {
  font-weight: bold;
  letter-spacing: -0.03rem;
}

h5.mad-page-title:not(:last-child) {
  margin-bottom: 1.45rem;
}

h3.mad-page-title:not(:last-child) {
  margin-bottom: 1.7rem;
}

.mad-page-title .mad-read-more {
  padding-left: 1.25rem;
}

.mad-page-title.with-carousel,
.mad-title.with-carousel {
  padding-right: 6rem;
}

h6.mad-page-title.with-carousel {
  font-size: 1.25rem;
  line-height: 1.5rem;
  padding-bottom: 4px;
}

.mad-pre-title {
  font-size: 1rem;
  line-height: 1.5rem;
  text-transform: uppercase;
}

.mad-sub-title {
  font-size: 1.25rem;
  line-height: 1.5rem;
}

.mad-pre-title:not(:last-child) {
  margin-bottom: 8px;
}

/* ----------------------------------------
            Paragraphs
        ---------------------------------------- */

p {
  line-height: inherit;
}

p:not(:last-child):not([class*="content-element"]) {
  margin-bottom: 1rem;
}

.req {
  color: #d94945;
}

.mad-text-small {
  font-size: 0.875rem;
  line-height: 1.5rem;
}

p.mad-text-small:not([class*="content-element"]):not(:last-child) {
  margin-bottom: 1.3125rem;
}

.mad-text-small {
  font-size: 0.9286rem;
  font-weight: 500;
}

.mad-text-medium {
  font-size: 1rem;
  line-height: 1.5rem;
}

.mad-text-big {
  font-size: 1.25rem;
  line-height: 2rem;
}

.mad-text-color2 {
  color: #8b9599;
}

.mad-text-color3 {
  color: #f2f2f2;
}

.mad-text-color4 {
  color: #515759;
}

/* ----------------------------------------
            Links
        ---------------------------------------- */

a {
  color: #26658c;
  font-weight: 500;
}

.mad-link,
a,
button {
  transition: all 0.5s ease;
}

.mad-link-color,
.mad-link {
  color: #26658c;
}

.mad-link-color:hover,
.mad-link:hover {
  color: #2e3133;
}

.mad-link-color-2 {
  color: #46a69c;
}

.mad-link-color-2:hover {
  text-decoration: underline;
}

/* ----------------------------------------
            Icons
        ---------------------------------------- */

[class*="mad-font-icon"] {
  line-height: 1;
}

/* ----------------------------------------
        Utils classes
    ---------------------------------------- */

.mad-d-none {
  display: none;
}

.mad-fw-bold {
  font-weight: bold;
}

.mad-tt-uppercase {
  text-transform: uppercase;
}

.mad-d-down-xxxl-none {
  display: none;
}

.mad-fw-down-xxxl-bold {
  font-weight: bold;
}

.alignleft {
  float: left;
}

.alignright {
  float: right;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.align-center {
  text-align: center;
}

img[class*="align"] {
  border-radius: 1rem;
  overflow: hidden;
}

img.alignleft {
  margin-right: 2rem;
}

img.alignright {
  margin-left: 2rem;
}

/* ----------------------------------------
    2. Layout
---------------------------------------- */

/* ----------------------------------------
        Preloader
    ---------------------------------------- */

.mad-preloader-circle,
.mad-preloader {
  position: fixed;
  width: 260vw;
  height: 260vw;
  margin-left: -130vw;
  margin-top: -130vw;
  background-color: #fff;
  z-index: 50;
  border-radius: 50%;
  -webkit-transition-property: -webkit-transform;
  transition-property: transform;
  -webkit-transition-delay: 0ms;
  transition-delay: 0ms;
  -webkit-transition-duration: 700ms;
  transition-duration: 700ms;
  -webkit-transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
}

.mad-preloader-circle {
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear;
}

.mad-preloader {
  left: 50%;
  top: 50%;
}

.mad-preloader-circle,
.mad-preloader.mad-preloader--disappearing {
  -webkit-transform: scale3d(0, 0, 0);
  transform: scale3d(0, 0, 0);
}

.mad-preloader-circle--appearing {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
}

/* ----------------------------------------
        Critical full screen message
    ---------------------------------------- */

.mad-body--has-critical-fullscreen-message {
  overflow: hidden !important;
}

.mad-fullscreen-message {
  position: fixed;
  z-index: 49;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: center;
  font-family: "Arial", serif;
  font-size: 1.875rem;
  line-height: 2.34375rem;
  color: #2e3133;
  background-color: #fff;
  padding: 2rem;
  overflow: auto;
}

.mad-fullscreen-message .mad-aligner-inner {
  vertical-align: middle;
}

.mad-fullscreen-message-before {
  font-size: 3.25rem;
  line-height: 4rem;
}

.mad-fullscreen-message-before:not(:last-child) {
  margin-bottom: 1rem;
}

.mad-fullscreen-message-before:empty,
.mad-fullscreen-message-after:empty {
  display: none;
}

/* ----------------------------------------
        Header
    ---------------------------------------- */

.mad-header {
  position: relative;
  z-index: 25;
}

[class*="mad-header-section--sticky"].mad-header-section--sticked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
}

.mad-header--transparent.mad-header--transparent-single {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 25;
}

.mad-header:not(.header-2):not(.header-3):not(.header-4)
  .mad-header-section--sticky-xl {
  background: #26658c;
}

.mad-header-section--sticky-xl .mad-header-items {
  position: relative;
}

.mad-pre-header {
  font-size: 0.875rem;
  line-height: 1.5rem;
  padding: 0.5rem 0;
  border-bottom: 1px solid #dce3e6;
}

.mad-pre-header .mad-our-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  column-gap: 1.5rem;
  row-gap: 0;
  font-weight: 500;
  width: 100%;
}

.mad-pre-header .mad-our-info .mad-info span {
  color: #2e3133;
  font-weight: 800;
}

.mad-pre-header .mad-header-item {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2rem;
}

.mad-quick-links {
  color: #8b9599;
}

.mad-config {
  display: flex;
  column-gap: 1.5rem;
}

.mad-config .mad-dropdown > a:after {
  content: "keyboard_arrow_down";
  font-family: "Material Icons";
  font-size: 1rem;
  margin-left: 0.25rem;
  max-width: 0.75rem;
  line-height: 1rem;
  display: inline-block;
  vertical-align: middle;
}

.mad-search-section {
  display: flex;
  justify-content: space-between;
  border: 2px solid #46a69c;
  background: #fff;
  border-radius: 1rem;
}

.mad-search-section .mad-custom-select {
  width: 100%;
}

.mad-search-section input,
.mad-search-section .mad-custom-select .mad-selected-option {
  height: 3.25rem !important;
  border: none !important;
}

.mad-search-section .mad-custom-select .mad-selected-option {
  line-height: 3.25rem;
}

.mad-search-section > .mad-col:first-child {
  width: 100%;
}

.mad-search-section > .mad-col:not(:first-child) {
  width: 100%;
  max-width: 16.5rem;
}

.mad-search-section > button:before,
.mad-search-section .mad-custom-select:before {
  content: "";
  position: absolute;
  width: 2px;
  height: 2.25rem;
  top: 50%;
  margin-top: -1.125rem;
  background: #dce3e6;
  left: 0;
  z-index: 2;
}

.mad-search-section > button {
  position: relative;
  padding: 0 18px;
  font-size: 1.75rem;
  line-height: 3rem;
  color: #46a69c;
}

.mad-actions {
  display: flex;
  justify-content: flex-end;
  column-gap: 2rem;
  font-size: 1rem;
  font-weight: 800;
}

.mad-actions .mad-col > a:not(.btn),
.mad-actions .mad-col > button:not(.btn) {
  position: relative;
  font-size: 1.75rem;
  color: #2e3133;
}

.mad-actions span {
  color: #2e3133;
}

.mad-actions > .mad-col {
  display: flex;
  align-items: center;
}

/* .mad-actions .mad-col > a:not(:last-child),
        .mad-actions .mad-col > button:not(:last-child) {
            margin-right: 0.5rem;
        } */

.mad-actions .mad-col > a span:not(.price-text),
.mad-actions .mad-col > button span:not(.price-text) {
  font-family: "Inter", sans-serif;
  position: absolute;
  width: 1.125rem;
  height: 1.125rem;
  background: #d94945;
  color: #fff;
  border-radius: 50%;
  text-align: center;
  line-height: 1.125rem;
  font-size: 11px;
  font-weight: 600;
  bottom: -4px;
  right: -6px;
}

.mad-header:not(.header-2):not(.header-3) .mad-shipping {
  color: #fff;
  text-align: right;
  width: 100%;
  line-height: 1.5rem;
}

.mad-shipping i {
  font-size: 1.5rem;
  margin-right: 0.5rem;
}

.mad-shipping span {
  font-weight: bold;
}

.mad-logo {
  display: block;
}

/* Header 2 */

.mad-promo {
  position: relative;
  background: #1f5373;
}

.mad-promo-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  row-gap: 1rem;
  column-gap: 1rem;
  padding: 2rem 0;
}

.promo-title {
  color: #f0e259;
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-weight: 500;
}

.promo-title span {
  text-transform: uppercase;
  font-weight: 800;
}

.promo-close {
  position: absolute;
  top: calc(50% - 1rem);
  right: 2rem;
  width: 2rem;
  min-width: 2rem;
  display: block;
  height: 2rem;
  border-radius: 50%;
  background: #dce3e6;
  color: #2e3133;
  text-align: center;
  font-size: 1.25rem;
  line-height: 2rem;
  z-index: 9;
}

.promo-close:after {
  content: "close";
  font-family: "Material Icons";
}

.header-2 .mad-shipping,
.header-3 .mad-shipping {
  color: #2e3133;
}

.header-2 .mad-navigation > li:not(:last-child),
.header-3 .mad-navigation > li:not(:last-child),
.header-4 .mad-navigation > li:not(:last-child) {
  border: none;
}

.header-2 .mad-navigation,
.header-3 .mad-navigation,
.header-4 .mad-navigation {
  column-gap: 2.5rem;
}

.header-2 .mad-navigation > li:hover > a,
.header-2 .mad-navigation > li.current-menu-item > a,
.header-3 .mad-navigation > li:hover > a,
.header-3 .mad-navigation > li.current-menu-item > a,
.header-4 .mad-navigation > li:hover > a,
.header-4 .mad-navigation > li.current-menu-item > a {
  background: none;
}

.header-2 .mad-header-section--sticky-xl {
  padding: 1.25rem 0;
}

.header-2 .mad-header-section {
  padding: 0;
}

.header-2 [class*="mad-header-section--sticky"].mad-header-section--sticked {
  background: #fff;
}

.header-2 .row:not(.col-no-space),
.header-4 .row:not(.col-no-space) {
  position: relative;
  margin-bottom: 0;
}

.header-2 .row:not(.col-no-space) > [class*="col"],
.header-4 .row:not(.col-no-space) > [class*="col"] {
  position: initial;
  padding-bottom: 0;
}

.header-2 .mad-navigation .mega-menu > .sub-menu,
.header-2 .mad-navigation .sub-menu {
  margin-top: 1rem;
}

.header-2 .mad-actions {
  padding-top: 0.75rem;
}

.header-2 .mad-icon-boxes.our-info .mad-icon-box {
  padding-left: 3.5rem;
}

.header-2 .mad-cat-toggle {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

.header-2 .mad-shipping i,
.header-3 .mad-shipping i {
  font-size: 1.25rem;
  margin-right: 4px;
}

.mad-header.header-2 .mad-navigation > li:not(.mega-menu) > .sub-menu,
.mad-header.header-3 .mad-navigation > li:not(.mega-menu) > .sub-menu,
.mad-header.header-4 .mad-navigation > li:not(.mega-menu) > .sub-menu {
  margin-left: -2rem;
}

.header-2
  [class*="mad-header-section--sticky"]
  .mad-header-sticky
  .mad-actions {
  padding-top: 0;
}

.header-2
  [class*="mad-header-section--sticky"].mad-header-section--sticked
  .mad-header-sticky
  .mad-icon-box,
.header-2
  [class*="mad-header-section--sticky"]:not(.mad-header-section--sticked)
  .mad-header-sticky
  .mad-actions,
.header-3
  [class*="mad-header-section--sticky"].mad-header-section--sticked
  .mad-header-sticky
  > .btn,
.header-3
  [class*="mad-header-section--sticky"]:not(.mad-header-section--sticked)
  .mad-header-sticky
  .mad-actions {
  display: none;
}

/* Header 3 */

.mad-header.header-3 {
  background: #1f5373;
}

.mad-header.header-3 .mad-header-section--sticky-xl {
  background: #1f5373;
  padding: 1rem 0;
}

.mad-header.header-3 .mad-pre-header .mad-header-item {
  width: 33.3333%;
}

.mad-header.header-3 .mad-pre-header .mad-header-item:nth-child(2) {
  justify-content: center;
}

.mad-header.header-3 .mad-pre-header .mad-header-item:nth-child(3) {
  justify-content: flex-end;
  column-gap: 1.5rem;
}

.mad-header.header-3 .mad-pre-header {
  background: #fff;
}

.mad-header.header-3 .mad-pre-header .mad-our-info .mad-info span {
  color: #26658c;
}

.mad-header.header-3 .mad-search-section {
  border: 0;
}

.mad-header.header-3 .mad-actions .mad-col > a,
.mad-header.header-3 .mad-actions .mad-col > span,
.mad-header.header-3 .mad-actions .mad-dropdown-title,
.mad-header.header-3 .mad-dropdown-title span.price-text {
  color: #fff;
}

.header-3 .mad-header-section {
  padding-bottom: 0;
}

.header-3 .mad-navigation > li.mad-special > a,
.header-3 .mad-navigation > li.mad-special:hover > a {
  background: #d94945;
  padding: 0 1rem;
  border-radius: 1rem;
  color: #fff;
}

.header-3 .mad-header-section--sticky-xl .mad-header-items {
  justify-content: space-between;
}

.header-3 .mad-header-section--sticky-xl .mad-header-items > *:last-child {
  flex: none;
}

.header-3 .mad-navigation .sub-menu {
  margin-top: 1rem;
}

.header-3 .mad-navigation > li:nth-child(2) {
  margin-left: -1.25rem;
}

/* Header 4 */

.mad-header.header-4 .mad-pre-header {
  background: #1f5373;
  color: #c5d3d9;
  border: none;
