/* ----------------------------------------
            Checkbox & Radio
        ---------------------------------------- */

.mad-toggled-fields:not(:last-child) {
  margin-bottom: 1rem;
}

input[type="checkbox"],
input[type="radio"] {
  display: none;
}

input[type="checkbox"] + label[for],
input[type="radio"] + label[for] {
  display: block;
  position: relative;
  font-size: 1rem;
  line-height: 1.5rem;
  padding-left: 3.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0;
  min-height: 1.5rem;
  color: #515759;
}

input[type="radio"] + label[for] {
  padding: 0 0 0 2rem;
}

input[type="checkbox"] + label[for]::before,
input[type="radio"] + label[for]::before {
  content: "";
  border: 2px solid #dce3e6;
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 1rem;
  position: absolute;
  left: 0;
  top: 0;
}

input[type="checkbox"] + label[for]::after,
input[type="radio"] + label[for]::after {
  font-family: "Material Icons";
  content: "done";
  color: #2e3133;
  font-size: 1.5rem;
  line-height: 2.5rem;
  position: absolute;
  left: 0;
  width: 2.5rem;
  text-align: center;
  top: 0;
  display: none;
}

input[type="checkbox"] + label[for]:not(:last-child),
input[type="radio"] + label[for]:not(:last-child) {
  margin-bottom: 0.5rem;
}

input[type="radio"] + label[for]:not(:last-child),
.mad-toggled-fields.size-2
  input[type="checkbox"]
  + label[for]:not(:last-child) {
  margin-bottom: 1rem;
}

input[type="checkbox"]:checked + label[for]::after,
input[type="radio"]:checked + label[for]::after {
  display: block;
}

input[type="radio"] + label[for]::after {
  content: "";
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  background-color: #2e3133;
  left: 0.375rem;
  top: 0.35rem;
}

input[type="radio"] + label[for]::before {
  border-radius: 50%;
  width: 1.25rem;
  height: 1.25rem;
}

.mad-toggled-fields.style-2 input[type="checkbox"] + label[for],
.mad-toggled-fields input[type="checkbox"] + label[for].style-2 {
  color: #26658c;
  font-weight: 500;
}

.mad-toggled-fields.size-2 input[type="checkbox"] + label[for],
.mad-toggled-fields.size-2 input[type="radio"] + label[for] {
  line-height: 1.5rem;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 2rem;
}

.mad-toggled-fields.size-2 input[type="checkbox"] + label[for]::before {
  height: 20px;
  width: 20px;
  border-radius: 0.5rem;
}

.mad-toggled-fields.size-2 label span {
  font-size: 0.875rem;
  line-height: 1.5rem;
  color: #747c80;
}

.mad-toggled-fields.size-2 input[type="checkbox"] + label[for]::after,
.mad-toggled-fields.size-2 input[type="radio"] + label[for]::after {
  font-size: 0.875rem;
  width: 1.5rem;
  line-height: 1.5rem;
  font-weight: bold;
}

.mad-label-checkbox.style-3 {
  color: #2e3133;
  cursor: text;
}

/* ----------------------------------------
            Select
        ---------------------------------------- */

.mad-custom-select {
  position: relative;
  min-width: 6rem;
}

.mad-custom-select.mad-over {
  z-index: 9;
}

.mad-custom-select .mad-selected-option {
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 3.5rem;
  line-height: 3.2rem;
  position: relative;
  padding-right: 2.625rem;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-top: 0;
  padding-bottom: 0;
}

.mad-custom-select.size-2 .mad-selected-option {
  height: 3rem;
  line-height: 2.75rem;
  border-radius: 1rem;
}

.mad-custom-select .mad-selected-option::after,
.mad-custom-select .mad-selected-option::before {
  content: "keyboard_arrow_down";
  font-family: "Material Icons";
  font-size: 1.25rem;
  line-height: inherit;
  position: absolute;
  color: #909a9d;
  top: 0;
  right: 1.375rem;
  max-width: 14px;
  -webkit-transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1),
    visibility 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1),
    visibility 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.mad-custom-select .mad-selected-option::before {
  content: "keyboard_arrow_up";
  opacity: 0;
  visibility: hidden;
}

.mad-custom-select .mad-options-list {
  font-size: 1rem;
  line-height: 1.5rem;
  text-align: initial;
  color: #8b9599;
  background-color: #fff;
  padding-bottom: 1rem;
  border: 2px solid #dce3e6;
  position: absolute;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: -1px;
  border-top: none;
  opacity: 0;
  visibility: hidden;
  max-height: 18.75rem;
  overflow: auto;
  -webkit-transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1),
    visibility 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1),
    visibility 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.mad-custom-select .mad-options-list > li {
  cursor: pointer;
  padding-top: 1rem;
  padding-right: 1.375rem;
  padding-left: 1.375rem;
  -webkit-transition: color 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: color 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.mad-custom-select .mad-options-list > li:hover,
.mad-custom-select .mad-options-list > li.mad-active {
  color: #2e3133;
}

.mad-custom-select.mad-opened .mad-options-list,
.mad-custom-select.mad-opened .mad-selected-option::before {
  opacity: 1;
  visibility: visible;
}

.mad-custom-select.mad-opened .mad-selected-option::after {
