  margin: 1rem;
  position: relative;
}

.mad-popup-inner .mad-popup-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 3rem;
  height: 3rem;
  background: #edf1f2;
  text-align: center;
  line-height: 3rem;
  border-radius: 1rem;
  font-size: 1.5rem;
}

.mad-popup-inner .mad-nl-title {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: bold;
  letter-spacing: 0;
  color: #2e2133;
}

.mad-nl-title span {
  color: #d94945;
}

.mad-popup-inner .mad-popup-close:hover {
  background: #26658c;
  color: #fff;
}

.mad-newsletter-form:not(:last-child) {
  margin-bottom: 1rem;
}

/* End of Newsletter Popup */

#mad-toggle-brands {
  display: none;
}

.hidden-section {
  padding: 1.875rem;
  border: 2px solid #dce3e6;
  border-radius: 1.5rem;
}

.hidden-section .row:not(.col-no-space) {
  margin-bottom: -1rem;
}

.hidden-section .row:not(.col-no-space) > [class*="col"] {
  padding-bottom: 1rem;
}

.hidden-section:not(:last-child) {
  margin-bottom: 0.5rem;
}

.hidden-section .account-action {
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.5rem;
  color: #2e3133;
}

.hidden-section form.one-line .btn {
  border-radius: 1rem;
}

.hidden-section .account-action > a {
  font-weight: bold;
}

.hidden-section .account-action > i {
  font-size: 1.75rem;
  margin-right: 0.5rem;
  color: #3a7397;
}

.hidden-section .account-action.active > i {
  color: #2e3133;
}

.hidden-section .content {
  padding-top: 1.5rem;
  font-size: 1rem;
  line-height: 1.5rem;
}

.mad-fixed-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 9;
  -webkit-box-shadow: 0px -3px 24px 0px rgba(46, 49, 51, 0.1);
  -moz-box-shadow: 0px -3px 24px 0px rgba(46, 49, 51, 0.1);
  box-shadow: 0px -3px 24px 0px rgba(46, 49, 51, 0.1);
}

.mad-fixed-bar-inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 0;
  column-gap: 2rem;
  row-gap: 2rem;
}

.mad-fixed-bar-inner .mad-col {
  display: flex;
  align-items: center;
  column-gap: 1.5rem;
}

.mad-fixed-bar-inner.mad-product .mad-product-title,
.mad-fixed-bar-inner.mad-product .mad-product-price {
  font-size: 1rem;
  line-height: 1.5rem;
}

.mad-fixed-bar-inner.mad-product .mad-product-image {
  max-width: 6rem;
}

.mad-fixed-bar-inner.mad-product .mad-actions {
  position: initial;
  opacity: 1;
  visibility: visible;
  flex-direction: row;
  column-gap: 0.5rem;
  margin: 0;
}

.mad-fixed-bar-inner.mad-product .quantity {
  max-width: 8rem;
}

.mad-fixed-bar-inner.mad-product .mad-actions > a {
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  font-size: 1.5rem;
}

.mad-video {
  display: block;
  position: relative;
}

.mad-video-link {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 0.75rem;
  font-size: 1.125rem;
  font-weight: 500;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  text-align: center;
  color: #fff;
}

.mad-video-link:before {
  content: "";
  width: 6.375rem;
  height: 4.5rem;
  /* background: url(../images/youtube.png) no-repeat; */
  display: block;
}

.mad-video img {
  position: relative;
  z-index: -1;
}

.mad-media-section {
  background-size: cover;
  background-repeat: no-repeat;
  height: 52rem;
  display: flex;
  align-items: center;
}

.mad-media-inner {
  width: 100%;
  font-size: 1.125rem;
  line-height: 2rem;
  padding-left: 6rem;
}

.mad-media-inner h1.mad-title {
  font-weight: 300;
  font-size: 5.5rem;
  line-height: 3.5rem;
}

.mad-media-inner h1.mad-title:not(:last-child) {
  margin-bottom: 1rem;
}

.mad-media-inner h2.mad-title {
  font-weight: 500;
}

.mad-media-inner h2.mad-title:not(:last-child) {
  margin-bottom: 0.25rem;
}

.mad-media-inner p:not(:last-child) {
  margin-bottom: 2.25rem;
}

.mad-specs-table {
  border: 2px solid #dce3e6;
  border-radius: 1rem;
}

.mad-specs-table:not(:last-child) {
  margin-bottom: 2rem;
}

.mad-specs-table > .mad-col {
  width: 50%;
}

.mad-spec-list {
  display: flex;
  flex-wrap: wrap;
  font-size: 1rem;
  line-height: 1.5rem;
}

.mad-spec-list > li .mad-col {
  width: 50%;
  display: flex;
}

.mad-spec-list > li .mad-col span:first-child {
  font-weight: bold;
  min-width: 15rem;
  color: #2e3133;
}

.mad-spec-list > li {
  width: 100%;
  padding: 1.25rem 1.875rem 1.1875rem;
  display: flex;
  flex-wrap: wrap;
}

.mad-spec-list > li:first-child {
  padding-top: 1.625rem;
}

.mad-spec-list > li:last-child {
  padding-bottom: 1.625rem;
}

.mad-spec-list > li:not(:last-child) {
  border-bottom: 1px solid #dce3e6;
}

